import 'core-js/stable';
import 'regenerator-runtime/runtime';
import * as model from './model.js';
import recipeView from './views/recipeView.js';
import searchView from './views/searchView.js';
import ResultView from './views/resultView.js';
import bookmarkView from './views/bookmarkView.js';
import addRecipeView from './views/addRecipeView.js';
// https://forkify-api.herokuapp.com/v2

///////////////////////////////////////
// console.log('Test2sa');

if (module.hot) {
  module.hot.accept();
}

const showRecipe = async function () {
  try {
    let id = window.location.hash.slice(1);
    if (!id) return;

    recipeView.loadSpinner();

    //1 loading the recipe
    await model.loadRecipe(id);
    // controlerServing();

    // 2) rendring recipie
    // const recipe = model.state.recipe;
    recipeView.render(model.state.recipe);
  } catch (err) {
    console.error(err);
    recipeView.renderError();
  }

  // 2) rendring recipie
};
// showRecipe();
// console.log('dddd')

const controlSearchResult = async function () {
  try {
    ResultView.loadSpinner();

    let query = searchView.getQuery();
    console.log(query);
    if (!query) query = 'pizza';
    await model.loadSearchResults(query);
    ResultView.render(model.state.search.results);
    // console.log(model.state.search.results);
  } catch (err) {
    console.log(err);
    recipeView.renderError();
  }
};

const controlerServing = function (serving) {
  model.updateServings(serving);

  recipeView.render(model.state.recipe);
};

const bookmarkgenerator = function () {
  if (!model.state.recipe.bookmarked) {
    model.bookmark(model.state.recipe);
  } else {
    model.deltebookmark(model.state.recipe.id);
  }
  recipeView.render(model.state.recipe);
  bookmarkView.render(model.state.bookmark);
  // console.log(model.state.recipe);
};

const bookmarkload = function () {
  bookmarkView.render(model.state.bookmark);
};

const controlAddRecipe = async function (newRecipe) {
  // console.log(newRecipe);
  try {
    recipeView.loadSpinner();
    setTimeout(function () {
      addRecipeView.onOff();
    }, 20);

    await model.uploadRecipe(newRecipe);

    recipeView.render(model.state.recipe);

    bookmarkView.render(model.state.bookmark);

    //close form window

    console.log(model.state.recipe);
  } catch (err) {
    console.error(err);
    addRecipeView.renderError(err.message);
  }
};

const init = function () {
  bookmarkView.addhandlerRender(bookmarkload);
  recipeView.addHandlerRender(showRecipe);
  recipeView.addHandlerUpdateServings(controlerServing);
  recipeView.addHandlerbookmark(bookmarkgenerator);
  searchView.addHandlerSearch(controlSearchResult);
  addRecipeView.addHandlerUpload(controlAddRecipe);
};

init();

