import { API_URL, KEY } from './config.js';
import { getJSON, sendJSON } from './helper.js';
export const state = {
  recipe: {},
  search: {
    query: '',
    results: [],
  },
  bookmark: [],
};

const createRecipeObj = function (data) {
  const recipe = data.data.recipe;

  return (state.recipe = {
    id: recipe.id,
    title: recipe.title,
    publisher: recipe.publisher,
    sourceUrl: recipe.source_url,
    image: recipe.image_url,
    servings: recipe.servings,
    ingredients: recipe.ingredients,
    cookingtime: recipe.cooking_time,
    ...(recipe.key && { key: recipe.key }),
  });
};

export const loadRecipe = async function (id) {
  try {
    const data = await getJSON(`${API_URL}${id}`);

    state.recipe = createRecipeObj(data);
    console.log(state.recipe);

    if (state.bookmark.some(b => b.id === id)) {
      state.recipe.bookmarked = true;
    } else {
      state.recipe.bookmarked = false;
    }
  } catch (err) {
    // alert(err);
    throw err;
  }
};

export const loadSearchResults = async function (query) {
  try {
    state.search.query = query;
    const data = await getJSON(`${API_URL}?search=${query}`);
    // console.log(data);

    state.search.results = data.data.recipes.map(el => {
      return {
        id: el.id,
        title: el.title,
        publisher: el.publisher,
        // sourceUrl: el.source_url,
        image: el.image_url,
      };
    });
    // console.log(state.search.results);
  } catch (err) {
    throw err;
  }
};

export const updateServings = function (newServings) {
  console.log(state);
  state.recipe.ingredients.forEach(ing => {
    ing.quantity = (ing.quantity * newServings) / state.recipe.servings;
  });

  state.recipe.servings = newServings;
};

const presistbookmark = function () {
  localStorage.setItem('bookmark', JSON.stringify(state.bookmark));
};

export const bookmark = function (recipe) {
  state.bookmark.push(recipe);

  if (recipe.id === state.recipe.id) {
    state.recipe.bookmarked = true;
  }

  presistbookmark();
};

export const deltebookmark = function (id) {
  const index = state.bookmark.findIndex(el => el.id === id);

  state.bookmark.splice(index, 1);

  if (id === state.recipe.id) {
    state.recipe.bookmarked = false;
  }

  presistbookmark();
};

const init = function () {
  const storage = localStorage.getItem('bookmark');
  if (storage) {
    state.bookmark = JSON.parse(storage);
  }
  console.log(state.bookmark);
};

export const uploadRecipe = async function (newRecipe) {
  try {
    const ingredients = Object.entries(newRecipe)
      .filter(entry => entry[0].startsWith('ingredient') && entry[1] !== '')
      .map(ing => {
        const ingArr = ing[1].replaceAll(' ', '').split(',');
        if (ingArr.length !== 3) {
          throw new Error('wrong format ');
        }
        const [quantity, unit, description] = ingArr;
        return { quantity: quantity ? +quantity : null, unit, description };
      });

    const recipe = {
      title: newRecipe.title,
      source_url: newRecipe.sourceUrl,
      image_url: newRecipe.image,
      publisher: newRecipe.publisher,
      cooking_time: newRecipe.cookingTime,
      servings: newRecipe.servings,
      ingredients,
    };

    // console.log(recipe);

    const data = await sendJSON(`${API_URL}?key=${KEY}`, recipe);
    console.log(data);
    state.recipe = createRecipeObj(data);
    bookmark(state.recipe);
    // console.log(data);
  } catch (err) {
    throw err;
  }
};

init();

