import View from './View.js';
import icon from 'url:../../img/icons.svg';

class bookmarkView extends View {
  _parentElement = document.querySelector('.bookmarks__list');
  _errorMessage = 'No bookmark yet. Find a nice recipe and bookmark it ;)';

  addhandlerRender(handler){
    window.addEventListener('load', handler)
  }

  _genereateMarkup() {
    // console.log(this._data);
    // this._parentElement.style.height = '150vh';
    // this._parentElement.style.overflow = 'auto';
    return this._data.map(this._genereateMarkupPreview).join('');
  }

  _genereateMarkupPreview(results, i) {
    // console.log(results, i);
    return `
      <li class="preview">
            <a class="preview__link preview" href="#${results.id}">
              <figure class="preview__fig">
                <img src="${results.image}" alt="${results.title}" />
              </figure>
              <div class="preview__data">
                <h4 class="preview__title">${results.title}</h4>
                <p class="preview__publisher">${results.publisher}</p>
               
              </div>
            </a>
          </li>
      
      `;
  }
}

export default new bookmarkView();
